@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: sans-serif;

}
.accordion{
    width: 100% !important;
    max-width: 80% !important;
    margin: 18px auto !important;
    color: #fff !important;
}
.accordionHeading{
background: #0a0303 !important;
padding: 14px 0;
border-radius:20px ;
box-shadow: -1px 1px 3px 2px rgba(124, 184, 124, 0.9);
}
.accordionHeading .container
{
 display: -webkit-flex !important;
 display: flex !important;   
 -webkit-align-items: center !important;   
         align-items: center !important;
 -webkit-justify-content: space-between !important;
         justify-content: space-between !important;
}
.accordionHeading p{
    letter-spacing: 1.2px;
    font-weight: 600;
    font-size: 15px;
    padding-top: 13px;
}
.accordionHeading span{
    cursor: pointer;
}

.accordionContent{
    /* background: #383a7f; */
    color: rgba(0,0,0,0.9);
    display: -webkit-flex;
    display: flex;
    height: 0;
    opacity: 0;
    box-shadow: 2px 2px 2px 3px rgba(233, 172, 172, 0);

    overflow: hidden;
    transition: all 400ms ease-in-out;
}
.accordionContent p{
    font-size: 15px;
    line-height: 1.6;

}
.show1{
    height: -webkit-max-content;
    height: max-content;
    margin-top:12px ;
    /* margin-bottom: -12px; */
    padding: 30px 10px;
    
    transition: all 800ms ease-in-out;
color: rgba(0,0,0,0.9);
    opacity: 1;
    border-radius: 10px;
    color: white;
    font-size: 20px;

    background: #050511 !important;
    box-shadow: -1px 1px 3px 2.5px rgba(46, 116, 25, 0.9);

}

* {
