.navbar {
  background: black !important;
  color: blanchedalmond !important;
  box-shadow: 2px 2px 5px -2px rgba(0, 0, 0, 0.5) !important;
}
.navbar-brand {
  font-size: 1.9rem !important;
  color: #ebe5e3 !important;
  background: rgb(71, 9, 3);
  font-weight: bolder;
  padding: 10px;
  border-radius: 50%;
  animation: side 1s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes side {
  0% {
    -webkit-transform: translatex(3px);
            transform: translatex(3px);
  }

  100% {
    -webkit-transform: translatex(-3px);
            transform: translatex(-3px);
  }
}

@keyframes side {
  0% {
    -webkit-transform: translatex(3px);
            transform: translatex(3px);
  }

  100% {
    -webkit-transform: translatex(-3px);
            transform: translatex(-3px);
  }
}
.navbar a {
  text-transform: capitalize;
  font-size: 1.3rem;
}
.main_active {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
.navbar a:hover {
  color: #3438db !important;

  transition: all 0.25s ease-in-out;
}
.carousel-background {
  background-color: black;
  /* height: 600px; */
}
