}
#header .btn-get-started {
  font-weight: 500 !important;
  font-size: 20px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  border: 2px solid black;
}
.header-img {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
